.my-learn-Announcement-show {
  width: 100%;
  display: block;
  background: #F6F7FB;
  min-width: 1366px;
}
.my-learn-Announcement-show .clearfix:after {
  content: '';
  display: block;
  height: 0;
  overflow: hidden;
  clear: both;
  zoom: 1;
}
.my-learn-Announcement-show .Announcement-show-pic {
  display: block;
  width: 100%;
  padding: 60px 0;
}
.my-learn-Announcement-show .Announcement-show-pic .text-back {
  vertical-align: middle;
  margin-left: 10px;
  font-size: 16px;
}
.my-learn-Announcement-show .Announcement-show-pic .top {
  height: 60px;
  background: #FFFFFF;
  box-shadow: 0 4px 15px rgba(45, 64, 101, 0.08);
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 120.7%;
  color: #2A283D;
  opacity: 0.99;
  position: fixed;
  padding: 20px 60px;
  text-align: center;
  top: 0;
  left: 0;
  width: 100%;
  display: block;
  z-index: 999;
}
.my-learn-Announcement-show .Announcement-show-pic .top .img {
  position: absolute;
  left: 56px;
  top: 50%;
  width: 20px;
  height: 22px;
  transform: translate(0, -50%);
}
.my-learn-Announcement-show .Announcement-show-pic .top .img img {
  display: block;
}
.my-learn-Announcement-show .Announcement-show-pic .Announcement-box {
  margin: 40px auto;
  display: block;
  width: 973px;
  position: relative;
  background: #FFFFFF;
  border-radius: 5px;
  padding: 40px 50px;
}
.my-learn-Announcement-show .Announcement-show-pic .Announcement-box .title {
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 205.2%;
  color: #2A283D;
  opacity: 0.99;
  text-align: center;
  display: block;
  margin-bottom: 20px;
}
.my-learn-Announcement-show .Announcement-show-pic .Announcement-box .text {
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 142.7%;
  color: #2A283D;
  opacity: 0.99;
  margin-top: 20px;
  min-height: 400px;
}
html {
  overflow-y: auto;
  overflow-x: auto;
}
